import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';


const dummyData = [
  { ticker: 'AAPL', price: 150, category: 'Tech' },
  { ticker: 'GOOGL', price: 2800, category: 'Tech' },
  { ticker: 'TSLA', price: 700, category: 'Auto' },
  { ticker: 'AMZN', price: 3300, category: 'Retail' },
];

const Asset = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState('ticker');
  const navigate = useNavigate();

  // Logout function
  const handleLogout = () => {
    localStorage.removeItem('authenticated');
    navigate('/'); // Redirect to login page
  };

  const handleSearch = e => {
    setSearchTerm(e.target.value);
  };

  const handleSort = e => {
    setSortBy(e.target.value);
  };

  const filteredData = dummyData
    .filter(item =>
      item.ticker.toLowerCase().includes(searchTerm.toLowerCase())
    )
    .sort((a, b) => (a[sortBy] > b[sortBy] ? 1 : -1));

    return (
      <div className="container mx-auto p-4">
        <h1 className="text-2xl font-bold mb-4">Asset List</h1>
        
        <div className="mb-4">
          <input 
            type="text"
            placeholder="Search by Ticker..."
            value={searchTerm}
            onChange={handleSearch}
            className="border p-2 rounded w-full"
          />
        </div>
  
        <div className="mb-4">
          <label className="mr-2">Sort by:</label>
          <select onChange={handleSort} className="border p-2 rounded">
            <option value="ticker">Ticker</option>
            <option value="price">Price</option>
          </select>
        </div>
  
        <table className="min-w-full bg-white">
          <thead>
            <tr>
              <th className="py-2 px-4 bg-gray-200">Ticker</th>
              <th className="py-2 px-4 bg-gray-200">Price</th>
              <th className="py-2 px-4 bg-gray-200">Category</th>
            </tr>
          </thead>
          <tbody>
            {filteredData.map((item, index) => (
              <tr key={index}>
                <td className="border px-4 py-2">{item.ticker}</td>
                <td className="border px-4 py-2">${item.price}</td>
                <td className="border px-4 py-2">{item.category}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };


export default Asset;
